<template>
  <div
    class="
      position-absolute
      top-0
      start-0
      vh-100
      vw-100
      d-flex
      align-items-end
      animated
    "
    :class="{ zIn: open, zOut: !open }"
  >
    <div
      id="set"
      class="
        d-flex
        flex-column
        justify-content-center
        bg-white
        shadow-lg
        p-3
        vh-100
        vw-100
        animated
      "
      :class="{ slideInUp: open, slideOutDown: !open }"
    >
      <p class="display-6 mt-5">Settings</p>
      <p>Google Calender Api</p>
      <input
        type="text"
        v-model="google.client_id"
        class="form-control mb-1"
        placeholder="CLIENT_ID: DDFG$§T"
      />
      <input
        type="text"
        v-model="google.api_key"
        class="form-control mb-1"
        placeholder="API_KEY: 2WR3..."
      />
      <p>Openweather</p>
      <input
        type="text"
        v-model="openweather.url"
        class="form-control mb-1"
        placeholder="URL: https://api.openweathermap.org/data/2.5/forecast/?q=London,de&appid=8021b4e46c033f8148e926c465998bee&units=metric&lang=de&cnt=10"
      />
      <p>Shelly</p>
      <input
        type="text"
        v-model="shelly.server_url"
        class="form-control mb-1"
        placeholder="server_url: ..."
      />
      <input
        type="text"
        v-model="shelly.device_id"
        class="form-control mb-1"
        placeholder="device_id: ..."
      />
      <input
        type="text"
        v-model="shelly.auth_key"
        class="form-control mb-1"
        placeholder="auth_key: ..."
      />
      <div class="flex-grow-1"></div>
      <button class="btn btn-dark" @click="save">Speichern</button>
    </div>
  </div>
</template>

<script>
export default {
  components: {},
  name: 'SettingComponent',
  emits: ['save'],
  props: {
    open: Boolean
  },
  data () {
    return {
      google: {
        client_id: '',
        api_key: '',
        data: null
      },
      openweather: {
        url: ''
      },
      shelly: {
        server_url: '',
        device_id: '',
        auth_key: ''
      }
    }
  },
  methods: {
    save: function () {
      localStorage.settings = JSON.stringify(this.$data)
      this.$emit('save', 'Daten gespeichert')
    }
  },
  mounted () {
    if (localStorage.settings) {
      const data = JSON.parse(localStorage.settings)
      this.google.client_id = data.google.client_id
      this.google.api_key = data.google.api_key
      this.openweather.url = data.openweather.url
      this.shelly.server_url = data.shelly.server_url
      this.shelly.device_id = data.shelly.device_id
      this.shelly.auth_key = data.shelly.auth_key
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.animated {
  animation-duration: var(--animate-duration);
  animation-fill-mode: both;
  animation-timing-function: ease-in-out;
}

@keyframes slideInUp {
  0% {
    border-radius: 50px;
    transform: translate3d(0, 100%, 0);
    visibility: visible;
    border-top-left-radius: 0px;
    border-top-right-radius: 0px;

  }

  100% {
    z-index: 3;
    border-radius: 0px;
    transform: translate3d(0, 0, 0);
    border-top-left-radius: 50px;
    border-top-right-radius: 50px;

  }
}

.slideInUp {
  border: 1px solid #e9e9e9;
  animation-name: slideInUp;
}

@keyframes slideOutDown {
  0% {
    border-radius: 0px;
    transform: translate3d(0, 0, 0);
    border-top-left-radius: 50px;
    border-top-right-radius: 50px;
  }

  100% {
    border-radius: 50px;
    visibility: hidden;
    transform: translate3d(0, 100%, 0);
    border-top-left-radius: 0px;
    border-top-right-radius: 0px;
  }
}

.slideOutDown {
  border-radius: 50px;
  animation-name: slideOutDown;
}

@keyframes zIn {
  0% {
    z-index: initial;
  }
}

.zIn {
  animation-name: zIn;
}

@keyframes zOut {
  99% {
    z-index: unset;
  }

  100% {
    z-index: -1;
  }
}

.zOut {
  animation-name: zOut;
}

.vh-90 {
  height: 90vh;
}
</style>
