<template>
  <div class="energie-meter-board p-1 ps-3 pe-3 ">
     <div class="progress d-block" @click="reload">
      <div
        class="progress-bar"
        :style="{ width: this.percent + '%' }"
      >
      &nbsp;
      </div>
    </div>
   <div class="mt-1 d-flex flex-row   justify-content-between">
    <span class="p-2 text-secondary">Solar</span>
    <span class="p-2 text-secondary">{{ this.power }} W</span>
   </div>
  </div>
</template>

<script>
import axios from 'axios'

export default {
  name: 'EnergieMeterBoard',
  data () {
    return {
      power: null,
      timestamp: null,
      showSetting: false,
      auth_key: '',
      device_id: '',
      server_url: '',
      max_power: 800,
      percent: 0
    }
  },
  methods: {
    reload: function () {
      this.power = 0
      const payload = 'id=' + this.device_id + '&auth_key=' + this.auth_key
      if (localStorage.settings) {
        axios.post(this.server_url, payload).then((response) => {
          this.power = parseFloat(
            response.data.data.device_status.meters[0].power
          )
          this.percent = (100 / this.max_power) * this.power
          this.timestamp = response.data.data.device_status.meters[0].timestamp
        })
      }
    }
  },
  mounted () {
    if (localStorage.settings) {
      const data = JSON.parse(localStorage.settings)
      this.auth_key = data.shelly.auth_key
      this.device_id = data.shelly.device_id
      this.server_url = data.shelly.server_url
      this.reload()
    }
  }
}
</script>

<style scoped>
.energie-meter-board {
  min-height: 60px;
  max-height: 20vh;
}
.progress {
  height: 10px;
  width: 100%;
}
.chart {
  display: block;
}
</style>
