<template>
  <div class="app d-flex flex-column vw-100 vh-100 antiselect overflow-hidden">
    <div class="d-flex justify-content-end p-3">
      <MenuIcon @click="toggleSettings" class="top-most" v-if="!open" />
      <XIcon @click="toggleSettings" class="top-most" v-if="open" />
    </div>
    <Tempe />
    <EnergieMeter />
    <tasks />
    <Settings v-on:save="onSave" :open="open" />
    <button
      v-if="isRefresh"
      @click="update"
      class="position-absolute btn btn-dark shadow u-btn"
    >
      Update
    </button>
  </div>
</template>

<script>
import Settings from './components/Settings.vue'
import Tempe from './components/Temp.vue'
import { MenuIcon, XIcon } from '../node_modules/vue-feather-icons'
import EnergieMeter from './components/EnergieMeter.vue'
import Tasks from './components/Tasks.vue'
export default {
  name: 'App',
  components: {
    MenuIcon,
    XIcon,
    Tempe,
    Settings,
    EnergieMeter,
    Tasks
  },
  data: () => ({
    registration: null,
    isRefresh: false,
    refreshing: false,
    open: false
  }),
  created () {
    document.addEventListener('serviceWorkerUpdateEvent', this.appUpdateUI, {
      once: true
    })
    navigator.serviceWorker.addEventListener('controllerchange', () => {
      if (this.refreshing) return
      this.refreshing = true
      window.location.reload()
    })
  },
  methods: {
    onSave: function (msg) {
      this.open = !this.open
    },
    toggleSettings: function () {
      this.open = !this.open
    },
    appUpdateUI: function (e) {
      this.registration = e.detail
      this.isRefresh = true
    },
    update: function () {
      this.isRefresh = false
      if (this.registration || this.registration.waiting) {
        this.registration.waiting.postMessage({ type: 'SKIP_WAITING' })
      }
    }
  }
}
</script>

<style>
:root {
  --animate-duration: 1s;
  --animate-delay: 1s;
  --animate-repeat: 1;
}

.animated {
  animation-duration: var(--animate-duration);
  animation-fill-mode: both;
}

.antiselect{
  user-select: none;
}
body {
  background-color: var(--bs-gray-100) !important;
}
.top-most{
  z-index: 12;
}
.app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
}
.u-btn {
  top: 10px;
  left: 10px;
  width: calc(100vw - 20px);
  height: 60px;
}
.backdrop {
  z-index: 10;
  backdrop-filter: blur(10px);
}
</style>
