<template>
  <div
    v-if="init"
    ref="task"
    class="ps-3 pe-3 d-flex flex-column justify-content-center animated flex-grow-1"
  >
    <!--Add buttons to initiate auth sequence and sign out-->
    <div
      class="mb-4 d-flex flex-row event"
      v-for="(task, index) in tasks"
      :key="task.id"
      :class="{ 'd-none': index > 3 && !allTasks }"
    >
      <span class="line rounded me-3 mb-2 mt-2" />
      <div class="d-flex flex-column flex-grow-1">
        <span class="text-start">{{ task.summary }}</span>
        <span class="text-end text-secondary">{{
          $moment.StringToDate(task.start).toLocaleDateString()
        }}</span>
      </div>
    </div>
    <span v-if="auth"
      class="rounded-pill text-white p-2 m-auto ps-5 pe-5 position-sticky"
      @click="allTasks = !allTasks"
      >more</span
    >
    <span id="authorize_button" class="rounded-pill text-white p-2 m-auto ps-5 pe-5" v-if="init && !auth" @click="handleAuthClick">Authorize</span>
    <span id="signout_button" class="rounded-pill text-white p-2 m-auto ps-5 pe-5" v-if="init && auth" @click="handleSignoutClick">Sign Out</span>
  </div>
  <div v-else>loading...</div>
  </template>

<script>

import { events } from '../assets/google.helper.js'

export default {
  components: { },
  name: 'TasksComponent',
  data () {
    return {
      info: null,
      tasks: [],
      allTasks: false,
      auth: false,
      init: false
    }
  },
  methods: {
    /**
     * Enables user interaction after all libraries are loaded.
     */
    maybeEnableButtons: function () {
      if (events.gapiInited && events.gisInited) {
        // document.getElementById('authorize_button').style.visibility = 'visible'
      }
    },

    /**
     *  Sign in the user upon button click.
     */
    handleAuthClick: function () {
      events.tokenClient.callback = async (resp) => {
        if (resp.error !== undefined) {
          throw resp
        }
        // document.getElementById('signout_button').style.visibility = 'visible'
        // document.getElementById('authorize_button').innerText = 'Refresh'
        await events.listUpcomingEvents()
      }

      if (window.gapi.client.getToken() === null) {
        // Prompt the user to select a Google Account and ask for consent to share their data
        // when establishing a new session.
        events.tokenClient.requestAccessToken({ prompt: 'consent' })
      } else {
        // Skip display of account chooser and consent dialog for an existing session.
        events.tokenClient.requestAccessToken({ prompt: '' })
      }
    },

    /**
     *  Sign out the user upon button click.
     */
    handleSignoutClick: function () {
      events.handleSignoutClick()
      // document.getElementById('content').innerText = ''
      // document.getElementById('authorize_button').innerText = 'Authorize'
      // document.getElementById('signout_button').style.visibility = 'hidden'
    },

    initHeigtht: function () {
      const boundingClientRect = this.$refs.task.getBoundingClientRect()
      console.log(boundingClientRect)
    }
  },
  mounted () {
    // document.getElementById('authorize_button').style.visibility = 'hidden'
    // document.getElementById('signout_button').style.visibility = 'hidden'

    const data = JSON.parse(localStorage.settings)
    // console.log(data)
    events.clientId = data.google.client_id
    events.apiKey = data.google.api_key
    this.tasks = events.tasks
    this.auth = events.auth
    this.init = events.init
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.animated {
  --max-height: 93px;
  --offset-top: -56px;
  --offset-bottom: calc(var(--max-height) - 100vh);
  height: var(--max-height);
  max-height: var(--max-height);
  background-color: var(--bs-gray-100);
  overflow: hidden;
}

.rounded-pill {
  background-color: var(--bs-purple);
  bottom: 10px;
}
.event .line {
  background-color: var(--bs-purple);
  min-width: 5px;
}
.text-secondary {
  font-size: 80%;
}
</style>
